import request from '@/request/api'
/**===========基本信息接口 */
//获取资产转移的列表
export function getListApi(data = {}) {
    return request({
        url: '/asset/scrappedAction/queryList.htm',
        method: 'post',
        data: data
    })
}
//新增/提交
export function insertApi(data = {}) {
    return request({
        url: '/asset/scrappedAction/addScrapped.htm',
        method: 'post',
        data: data
    })
}
//更新/提交
export function updateApi(data = {}) {
    return request({
        url: '/asset/scrappedAction/updateScrapped.htm',
        method: 'post',
        data: data
    })
}
//删除
export function deleteApi(data = {}) {
    return request({
        url: '/asset/scrappedAction/delete.htm',
        method: 'post',
        params: data
    })
}
//详情
export function getDetailsApi(id = "") {
    return request({
        url: '/asset/scrappedAction/detail.htm',
        method: 'post',
        params: { id: id }
    })
}